<template>
  <div>
    <van-sticky>
      <van-nav-bar title="提现" left-arrow @click-left="onClickLeft" />
    </van-sticky>

    <div class="userWrap">
      <van-action-sheet
        :close-on-click-overlay="false"
        v-model="show"
        :actions="option"
        @select="onSelect"
      />
      <!-- <van-field label="验证码" placeholder="请输入验证码" v-model="userInfo.pid_member"  /> -->
      <van-cell-group>
        <van-cell title="选择提现地址" is-link @click="showweek" :value="typename" />
        <van-field
          label="提现积分数量"
          placeholder="请输提现积分数量"
          v-model="userInfo.integral"
          @input="getUsdt"
        />
        <van-field label="对应USDT数量" placeholder="请确认新密码" v-model="userInfo.usdt" />
        <van-field label="提现金额" placeholder="请确认新密码" v-model="userInfo.money" />
      </van-cell-group>
      <button class="submitBtn" @click="updateUser">提交</button>
    </div>
     <van-dialog
      v-model="showPassword"
      title="交易密码"
      show-cancel-button
      @confirm="pay"
      @cancel="cancelPay"
      :before-close="beforePasswordClose"
    >
      <van-password-input :value="password" info="密码为 6 位数字类型" style="position:initial" />
    </van-dialog>
    <van-number-keyboard
      id="keyboard-panel"
      :show="showPassword"
      @input="onInput"
      @delete="onDelete"
      :z-index="9999"
      style="color:black"
    />
  </div>
</template>

<script>
import {
  userInfo,
  uploadImg,
  updateUser,
  cashWithdrawal,
  walletList,
  rechargeInfo
} from "@/api/personal";
export default {
  data() {
 
    return {
      show: false,
      option: [],
      
      showPassword:false,
      password:"",
      typename: "",
      week: {},
      userInfo: {
        integral: "",
        transaction:'',
        usdt: "0.0",
        money: "0.0",
        wallet_id: ""
      },
      img: ""
    };
  },
  created() {
    this.getUser();
    rechargeInfo().then(resp => {
      this.week = resp;
    });
  },
  methods: {


                beforePasswordClose(action, done) {
      if (action === "confirm") {
        if (this.password.length != 6) {
          done(false);
        } else {
          done();
        }
      } else {
        done();
      }
    },
    onInput(key) {
      this.password = (this.password + key).slice(0, 6);
      this.zIndex = 9999;
    },
    onDelete() {
      this.password = this.password.slice(0, this.password.length - 1);
    },
    cancelPay() {
      this.password = "";
    },
    pay() {
        this.userInfo.transaction=this.password
        cashWithdrawal(this.userInfo).then(res => {
          if (res.errcode == 0) {
            this.$toast("提现成功");
            setTimeout((res)=>{
                this.$router.push({name:"MyCredit"})
            },800)
            // this.userInfo = { integral: "", usdt: "0.0", money: "0.0" };
          } else {
            this.$toast(res.msg);
          }
        });
    },
      showweek(){
          if(this.option.length==0){
        this.$toast("请先添加提现地址");
          }else{
          this.show=true

          }
      },
    getUsdt() {
      this.userInfo.usdt =this.cal.accMul(this.userInfo.integral,this.week[0].config_value);
      this.userInfo.money = this.cal.accMul(this.userInfo.usdt,this.week[1].config_value);
    },
    getUser() {
      walletList().then(res => {
        res.map((i, x) => {
          this.option.push({
            name: i.money_address,
            type: i.id
          });
        });
      });
    },
    onSelect(item) {
      this.typename = item.name;
      this.userInfo.wallet_id = item.type;
      this.show = false;
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    upImg(file) {
      this.image = file.content.substring(file.content.indexOf(",") + 1);
      uploadImg(this.image, "").then(resp => {
        this.userInfo.wechat_img = resp.img;
      });
    },
    // 提交
    updateUser() {
      if (this.userInfo.integral == "") {
        this.$toast("请输入积分数量");
        return false;
      }else if(this.userInfo.wallet_id == ""){
        this.$toast("请选择提现地址");

      } else {
          this.showPassword=true
        // cashWithdrawal(this.userInfo).then(res => {
        //   if (res.errcode == 0) {
        //     this.$toast("提现成功");
        //     this.userInfo = { integral: "", usdt: "0.0", money: "0.0" };
        //   } else {
        //     this.$toast(res.msg);
        //   }
        // });
      }
    }
  }
};
</script>
<style lang="scss" scoped>
/deep/ .van-dialog{
    top:35%;position: fixed;
    .van-dialog__header{
        height: 45px;
    }
    .van-password-input__security{
        margin-bottom: 10px;
    }
    .van-password-input__info{
        padding-bottom: 20px;
    }
}
/deep/ .van-field__label {
  width: 120px;
}
.van-button--primary {
  background: white;
  color: #28a6ff;
  border: 1px solid #28a6ff;
  border-radius: 20px;
}
.van-cell {
  padding: 15px 16px 19px;
}
.userImg {
  width: 39px;
  height: 39px;
}
.submitBtn {
  display: block;
  width: 345px;
  height: 40px;
  line-height: 40px;
  color: #fff;
  font-size: 18px;
  border-radius: 20px;
  background: #28a6ff;
  margin: 66px auto;
  border: 0 none;
}
</style>
